<template>
    <van-list class="repair-content"
        finished-text="没有更多了"
        :finished="finished"
        @load="getList"
    >
        <div class="tab-head">
            <van-tabs v-model="params.status" @click="changTab">
                <van-tab title="未勘察" name="pending"></van-tab>
                <van-tab title="进行中" name="processing"></van-tab>
                <van-tab title="已完成" name="done"></van-tab>
            </van-tabs>
        </div>
        <div v-show="params.status == 'pending'">
            <repair-block :data="{}" style="margin-top: 4.3vw">
                <div slot="btn">
                    <van-button type="primary" size="small" to="/repairDetail?type=1">去勘察</van-button>
                </div>
            </repair-block>
            <repair-block :data="{}" style="margin-top: 4.3vw">
                <div slot="btn">
                    <van-button type="primary" size="small" to="/repairDetail">去勘察</van-button>
                </div>
            </repair-block>
            <repair-block :data="{}" style="margin-top: 4.3vw">
                <div slot="btn">
                    <van-button type="primary" size="small" to="/repairDetail">去勘察</van-button>
                </div>
            </repair-block>
            <repair-block :data="{}" style="margin-top: 4.3vw">
                <div slot="btn">
                    <van-button type="primary" size="small" to="/repairDetail">去勘察</van-button>
                </div>
            </repair-block>
            <repair-block :data="{}" style="margin-top: 4.3vw">
                <div slot="btn">
                    <van-button type="primary" size="small" to="/repairDetail">去勘察</van-button>
                </div>
            </repair-block>
            <repair-block :data="{}" style="margin-top: 4.3vw">
                <div slot="btn">
                    <van-button type="primary" size="small" to="/repairDetail">去勘察</van-button>
                </div>
            </repair-block>
        </div>
        <div v-show="params.status == 'processing'">
            <repair-block :data="{}" style="margin-top: 4.3vw">
                <div slot="btn">
                    <van-button type="primary" size="small" to="/repairDetail">维修记录</van-button>
                </div>
            </repair-block>
        </div>
        <div v-show="params.status == 'done'">
            <repair-block :data="{}" style="margin-top: 4.3vw" :status="2"></repair-block>
        </div>
    </van-list>
</template>
<script>
import { Tab, Tabs, Button, List  } from 'vant';
import RepairBlock from './repairBlock.vue'
export default {
    name: 'repair',
    components: {
        [Tab.name]: Tab,
        [Tabs.name]: Tabs,
        [Button.name]: Button,
        [List.name]: List,
        RepairBlock
    },
    created() {
        this.getList();
    },
    data() {
        return {
            loading: false,
            finished: false,
            params: {
                pageNum: 1,
                pageSize: 10,
                searchCount: false,
                status: "pending"
            },
            total: 0,
            list: []
        }
    },
    methods: {
        getList(params) {
            this.loading = true;
            const searchParams = Object.assign(this.params, params)
            this.$axios.post('/h5/repair/page', searchParams).then(res => {
                this.loading = false;
                this.total = res.total;
                this.list = this.list.concat(res.list)
                if (!res.pages || (this.list.length == this.total) || !res.list.length) {
                    this.finished = true;
                }
                console.log(res)
            })
        },
        changTab() {
            this.getList()
        }
    }
}
</script>
<style lang="less" scoped>
.repair-content {
    padding-top: 11.73333vw;
    padding-bottom: 4.3vw;
    .tab-head {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        z-index: 1;
    }
}
</style>